<template>
  <div class="video-wrapper" :style="`width:${w || ps(wps,ww)}px,height:${h || ps(hps,wh)}px`">
    <div v-if="loading" class="loader">Loading...</div>
    <video
    class="videoref"
      :src="vsrc"
      controlsList="notimeline noremoteplayback nodownload nofullscreen noremoteplayback"
      ref="video"
      :class="classes"
      :controls="c"
      :width="w || ps(wps,ww)"
      :height="h || ps(hps,wh)"
      autoplay
    />
  </div>
</template>

<script>
import Hls from "hls.js";
export default {
  name: "HlsPlayer",
  props: {
    src: String,
    w: Number,
    h: Number,
    wps: Number,
    hps: Number,
    c: Boolean,
    classes:String
  },
  data: () => ({
    vsrc: null,
    ww: null,
    wh: null,
    loading:false
  }),
  computed: {
    video() {
      return this.$refs.video;
    },
    hls() {
      return new Hls({
        enableWorker: true,
        lowLatencyMode: true,
        abrEwmaFastVoD: 3.0,
        testBandwidth: true,
      });
    },
  },
  beforeUpdate: () => {
    //  this.hls.destroy()
  },
  created() {
    this.vsrc = this.src;
    this.reportWindowSize();
    this.$nextTick(() => {
      window.onresize = this.reportWindowSize;
    });
  },
  watch: {
    src: function (val) {
      this.hls.detachMedia(this.video);
      this.video.src = null;
      this.video.srcObject = null;
      this.hls.attachMedia(this.video);
      this.hls.loadSource(val);
      this.$nextTick(() => {
        // console.log(this.video);
        this.video.addEventListener("canplay", (event) => {
          this.video.play();
        });
      });
    },
  },
  mounted() {
    this.startVideo();
    this.joinVideoHls();
//           this.video.addEventListener('waiting', (event) => {
//   console.log('Video is waiting for more data.');
// });
  },
  methods: {
    ps(s,f){
    return ((s/100 )* f).toFixed()
    },
    reportWindowSize() {
      this.wh = window.innerHeight.toFixed();
      this.ww = window.innerWidth.toFixed();
    },
    startVideo() {
      this.hls.attachMedia(this.video);
      this.hls.loadSource(this.src);
    },
    joinVideoHls() {
      this.hls.on(Hls.Events.MEDIA_ATTACHED, function () {
        console.log("video and hls.js are now bound together !");
      });
      this.hls.on(Hls.Events.FRAG_LOADING, function () {
       this.loading = true
      });
      this.hls.on(Hls.Events.FRAG_LOADED, function () {
       this.loading = false
      });
      let vm = this
      this.hls.on(Hls.Events.ERROR, function (event, data) {
        if (data.fatal) {
          switch (data.type) {
            case Hls.ErrorTypes.NETWORK_ERROR:
              // try to recover network error
              console.log("fatal network error encountered, try to recover");
              vm.hls.startLoad();
              break;
            case Hls.ErrorTypes.MEDIA_ERROR:
              console.log("fatal media error encountered, try to recover");
              vm.hls.recoverMediaError();
              break;
            default:
              // cannot recover
              vm.hls.destroy();
              break;
          }
        }
      });
    },
  },
};
</script>

